.loader {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  height: 100%;
  width: 100%;
}
.lds-ripple {
  margin: 75px auto;

  position: absolute;
  width: 0;
  top: 37%;
  left: 50%;
  height: 0;
  transform: translateX(-75px);
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #eb1212;
  left: 50%;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 75px;
    left: 75px;
    width: 0;
    height: 0;
    opacity: 0;
  }
  4.9% {
    top: 75px;
    left: 75px;
    width: 0;
    height: 0;
    opacity: 0;
  }
  5% {
    top: 75px;
    left: 75px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 150px;
    height: 150px;
    opacity: 0;
  }
}
